import MyPagination from "../../../components/MyPagination";
import mixins from "../../../utils/mixins";
import {common} from "../../../utils/api/url";
import {MessageError, MessageSuccess, MessageWarning} from "../../../utils/utils";
import LayoutTb from "../../../components/Layout/LayoutTb";
import MySelect from "../../../components/MySelect";
import MyUserType from "../../../components/utils/MyUserType";
import MyDatePicker from "../../../components/MyDatePicker";
import MyInputButton from "../../../components/search/MyInputButton";
import DialogNormal from "../../../components/utils/dialog/DialogNormal";
import DrawerRight from "../../../components/utils/dialog/DrawerRight";
import stuAuthValidater from "../../../utils/validater/stuAuthValidater";
export default {
  mixins: [mixins, stuAuthValidater],
  components: {MyPagination,LayoutTb,MySelect,MyUserType,MyDatePicker,MyInputButton,DialogNormal,DrawerRight},
  data(){
    return {
      tableData: [],
      searchDate: [],
      typeList: [],
      selData: [],
      visible: false,
      modalVisible: false,
      modalAuthVisible: false,
      dialogLoading: false,
      visibleConfim: false,
      autoLoading: false,
      drawerVisible: false,
      drawerLoading: false,
      tipsText: this.$t('APP手机号认证失败提示信息'),
      clearTime: '',
      action: '',
      subTitle: '',
      phoneOwner: '',
      status: '',
      searchKey: '',
      uploadFile: common.file_import_modal + "?fileName=" + encodeURIComponent(this.$t("手机号录入模版.xls")),
      uploadAction: common.phone_limit_import,
      uploadResult: {},
      uploadProcess: '',
      form: {
        id: '',
        phone: '',
        name: '',
        no: '',
        des: '',
        phoneOwner: '1'
      },
      formAuth: {
        switch: false,
        content: ''
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        phoneOwner: this.phoneOwner,
        status: this.status,
        searchKey: this.searchKey
      };

      this.$axios.get(common.phone_limit_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    initConfig(){
      this.$axios.get(common.phone_limit_tips_get).then(res => {
        if (res.data.data){
          this.formAuth = {
            switch: res.data.data.status,
            content: res.data.data.des
          };
        }
      });
    },
    addInfo(){
      this.modalVisible = true;
    },
    settingInfo(){
      this.initConfig();
      this.modalAuthVisible = true;
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    handleSelect(data){
      this.form.type = data;
    },
    editInfo(row){
      this.form = {
        id: row.id,
        phone: row.phone,
        name: row.name,
        no: row.phone_no,
        des: row.des,
        phoneOwner: row.phone_owner
      };
      this.modalVisible = true;
    },
    deleteInfo(row){
      this.form.id = row.id;
      this.subTitle = row.phone;
      this.visibleConfim = true;
    },
    importInfo(event){
      this.drawerVisible = true;
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let url = "";
      let params = {
        id: this.form.id
      };
      url = common.phone_limit_del;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    },
    cancelDialog(){
      this.modalVisible = false;
      this.modalAuthVisible = false;
    },
    closeDialog(event){
      this.form = {
        id: '',
        phone: '',
        name: '',
        no: '',
        des: '',
        phoneOwner: '1'
      };
      this.formAuth = {
        switch: false,
        content: ''
      };
      this.subTitle = "";
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
      if (this.$refs['formAuth']){
        this.$refs['formAuth'].resetFields();
      }
    },
    okDialog(event){
      let url = "";
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            phone: this.form.phone,
            name: this.form.name,
            phoneNo: this.form.no,
            des: this.form.des,
            phoneOwner: this.form.phoneOwner
          };

          if (this.form.id != ""){
            params["id"] = this.form.id;
          }
          url = common.phone_limit_save;
          params = this.$qs.stringify(params);
          this.$axios.post(url, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.modalVisible = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    okAuthDialog(event){
      let url = "";
      this.$refs['formAuth'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            status: this.formAuth.switch,
            des: this.formAuth.content
          };

          url = common.phone_limit_tips_set;
          params = this.$qs.stringify(params);
          this.$axios.post(url, params).then(res => {
            if (res.data.code == 200){
              this.modalAuthVisible = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    fliterTable(value, row, column){
      for (let item in value){
        if (item == 'status'){
          this.status = value[item][0];
        }else if (item == 'owner'){
          this.phoneOwner = value[item][0];
        }
      }
      this.init();
    },
    selOwner(type){
      this.form.phoneOwner = type;
    },
    autoNo(){
      this.autoLoading = true;
      this.$axios.get(common.phone_limit_no).then(res => {
        if (res.data.code == 200){
          this.form.no = res.data.data;
        }else {
          MessageError(res.data.desc);
        }
        this.autoLoading = false;
      });
    },
    getRowKeys(row) {
      return row.id
    },
    handleSelectionChange(data){
      this.selData = data;
    },
    mutiOprInfo(type){
      let arr = [];
      let params = {};
      let url = "";
      if (this.selData.length == 0){
        MessageWarning(this.$t("请选择需要操作的数据"));
        return;
      }
      for (let i = 0; i < this.selData.length; i++){
        arr.push(this.selData[i].id);
      }
      if (type == 1){
        params = {
          id: arr.join(),
          statsu: true
        };
        url = common.phone_limit_status;
        this.$axios.get(url, {params: params}).then(res => {
          if (res.data.code == 200){
            MessageSuccess(res.data.desc);
            this.init();
          }else {
            MessageError(res.data.desc);
          }
        });
      }else if (type == 2){
        params = {
          id: arr.join(),
          statsu: false
        };
        url = common.phone_limit_status;
        this.$axios.get(url, {params:params}).then(res => {
          if (res.data.code == 200){
            MessageSuccess(res.data.desc);
            this.init();
          }else {
            MessageError(res.data.desc);
          }
        });
      }else if (type == 3){
        params = {
          id: arr.join()
        };
        url = common.phone_limit_del;
        params = this.$qs.stringify(params);
        this.$axios.post(url, params).then(res => {
          if (res.data.code == 200){
            MessageSuccess(res.data.desc);
            this.init();
          }else {
            MessageError(res.data.desc);
          }
        });
      }
    },
    closeDrawerDialog(event){
      this.uploadProcess = '';
      this.uploadResult = {};
      this.drawerVisible = event;
    },
    cancelDrawDialog(){
      this.uploadProcess = '';
      this.uploadResult = {};
      this.drawerVisible = false;
    },
    uploadSuccess(res, file){
      this.uploadProcess = res.desc;

      if (res.code == 200){
        this.uploadResult = res.data ? res.data : [res.desc];
      }else {
        if (res.data){
          this.uploadResult = res.data;
        }else {
          this.uploadResult = [res.desc];
        }
      }
    },
    uploadError(res, file){
      this.uploadProcess = res.data.data;
    }
  }
}
